import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the ChatbotProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ChatbotProvider {

  constructor(public http: HttpClient) {
   
  }

  public responseToBot(response: string) {
    return this.http.get(encodeURI('http://localhost:3000/api/v1/response?question=' + response.trim())).toPromise();
  }

  public initConversation() {
    return this.http.get(encodeURI('http://localhost:3000/api/v1/response?question=')).toPromise();
  }

}



